
.form__fieldset {
    border-radius: 65px;
    border: 10px inset hsla(204, 67%, 58%, 0.842);
    background-color: rgba(255, 255, 255, 0.473);
}

.form__legend {
    text-align: center;
    font-weight: bold;
    font-size: large;
    color: rgb(23, 23, 23);
    padding: 0 10px;
}

.form__result {
    text-align: center;
    padding: 10px 0;
}

.form__paragraph {
    margin-left: 5px;
}

.form__button {
    display: flex;
    justify-content: center;
    margin: auto;
    max-width: 600px;
    padding: 15px;
    border: none;
    border-radius: 20px;
    background-color: hsla(204, 67%, 58%, 0.842);
    font-weight: bold;
    font-size: large;
    letter-spacing: 0.8px;
    text-transform: uppercase;
}

.form__button:hover {
    background-color: hsla(204, 48%, 44%, 0.842);
}

.form__button:active {
    background-color: hsla(204, 47%, 27%, 0.842);
}

.label__title {
    width: 250px;
    display: inline-block;
    margin-left: 5px;
}

.label__input {
    border: 2px solid rgb(130, 125, 125);
    padding: 10px;
    border-radius: 10px;
    max-width: 135px;
}


@media (max-width: 543px) {
    .label__input {
        max-width: none;
    }
}

.label__input--disabled {
    background-color: rgb(255, 255, 255);
}

.label__input--pln {
    max-width: 300px;
}